  title: "Things Need-to-Know About Mercury",
  parentId: "1",
  text: "<h3>SMALLEST</h3><p>Mercury is the smallest planet in our solar systemonly slightly larger than Earth's Moon.</p>" + "<h3>INSIDER</h3><p>It is the closest planet to the Sun at a distance of about 36 million miles (58 million kilometers) or 0.39 AU.</p>" + "<h3>LONG DAYS, SHORT YEARS</h3><p>One day on Mercury (the time it takes for Mercury to rotate or spin once with respect to the stars) takes 59 Earth days. One day-night cycle on Mercury takes 175.97 Earth days. Mercury makes a complete orbit around the Sun (a year in Mercury time) in just 88 Earth days.</p>" + "<h3>ROUGH SURFACE</h3><p>Mercury is a rocky planet, also known as a terrestrial planet. Mercury has a solid, cratered surface, much like the Earth's moon.</p>"
