  text: "The smallest planet in our solar system has a big presence in our collective imagination. Scores of science fiction writers have been inspired by Mercury, including Isaac Asimov, C. S. Lewis, Ray Bradbury, Arthur C. Clarke and H. P. Lovecraft. Television and film writers, too, have found the planet an ideal location for storytelling. In the animated television show Invader Zim, Mercury is turned into a prototype giant spaceship by the extinct Martians. And in the 2007 film Sunshine, the Icarus II spacecraft goes into orbit around Mercury to rendezvous with the Icarus I." + "In the comic strip Calvin and Hobbes, Calvin and his classmate Susie give a presentation about Mercury, in which Calvin's contribution is full of questionable information: 'The planet Mercury was named after a Roman god with winged feet,' says Calvin. 'Mercury was the god of flowers and bouquets, which is why today he is a registered trademark of FTD florists. Why they named a planet after this guy, I can't imagine.'"
