  title: "Introduction",
  parentId: "8",
  text: "<p>Jupiter has a long history surprising scientistsall the way back to 1610 when Galileo Galilei found the first moons beyond Earth. That discovery changed the way we see the universe.</p>" + "<p>Fifth in line from the Sun, Jupiter is, by far, the largest planet in the solar system  more than twice as massive as all the other planets combined.</p>" + "<p>Jupiter's familiar stripes and swirls are actually cold, windy clouds of ammonia and water, floating in an atmosphere of hydrogen and helium. Jupiters iconic Great Red Spot is a giant storm bigger than Earth that has raged for hundreds of years.</p>"
